<template>
<div>
  <ons-page>
    <v-ons-list modifier="longdivider">
      <v-ons-list-item v-for="mes in messages">
	{{mes}}
      </v-ons-list-item>
    </v-ons-list>
    <v-ons-alert-dialog modifier="rowfooter"
			:visible.sync="show">
      <span slot="title">入力画面</span>
      <div class="center">
        <v-ons-input placeholder="ひとこと" float
		     v-model="message" />
      </div>
      <template slot="footer">
        <v-ons-alert-dialog-button @click="show = false">
	  キャンセル
	</v-ons-alert-dialog-button>
        <v-ons-alert-dialog-button @click="handleOk">
	  追加
	</v-ons-alert-dialog-button>
      </template>
    </v-ons-alert-dialog>
    <v-ons-fab
      position="bottom right"
      @click="show = true">
      <v-ons-icon icon="md-plus"></v-ons-icon>
    </v-ons-fab>
  </ons-page>
</div>
</template>
<script>
export default {
  name: 'Page1',
  data () {
    return {
      messages: [],
      message: "",
      show: false
    }
  },
  methods: {
    handleOk () {
      this.messages.push(this.message);
      this.message = "";
      this.show = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>




